            <div class="user-image" style="left: -40px;">
              <img src="@/assets/img/catbackground.png" alt="" style="width: 50px;height: 50px;left: 8.9%;position: absolute;"/>
            </div>
            <div class="username" style="margin-right: 100px;">{{ getUsername }}</div>
            <img src="~@/assets/img/line.png" alt="" class="separator-line"><!--  -->
          </div>
          <!---->
          <div class="">
            <!--  -->
            <h3 @click="navigateTo('BuyerCart')" class="black-text" style="cursor: pointer;"></h3>
            <h3 @click="navigateTo('BuyerLikes')" class="black-text" style="cursor: pointer;"></h3>
            <h3 @click="navigateTo('buyerHistory')" class="black-text" style="cursor: pointer;"></h3>
            <h3 @click="navigateTo('BuyerShowCat')" class="black-text" style="cursor: pointer;"></h3>
